.Results {
  padding-top: 0;
}

.Results__intro {
  padding: 74px 80px;
  border-radius: 4px;
  background-color: #000;

  color: #FFF;
  font-size: 48px;
  line-height: 64px;
}

.Results__table {
  width: 100%;
  border: none;
  table-layout: fixed;
  text-align: left;
  line-height: 2.5;
  font-weight: bold;
  font-size: 32px;
  color: #333;
}
.Results__table td,
.Results__table th {
  border: none;
  padding: 0;
}
.Results__table-head-title {
  font-size: 48px;
}
.Results__table-head-scores {
  text-align: right;
}
.Results__table-head-delimiter {
  height: 2px;
  background-color: #cccccc;
  margin: 0 45px;
}
.Results__table-head-scores,
.Results__table-result-score {
  width: 100px;
}
.Results__result-index {
  display: inline-block;
  margin-right: 40px;
  width: 40px;

  color: #ee8208;
  text-align: right;
}
.Results__table-result-score {
  color: #999;
  text-align: right;
}

.Results__go-to-admin-container {
  text-align: right;
}
.Results__go-to-admin {
  font-size: 24px;
  color: #ee8208;
  cursor: pointer;
}



