.AdminResults__header {
  font-size: 32px;
  color: #000;
  text-align: center;
}

.AdminResults__list {
  list-style: none;
  width: 1000px;
  padding: 0;
  margin: 0 auto;
  text-align: left;

  font-size: 24px;
}

.AdminResults__info-item + .AdminResults__info-item {
  display: inline-block;
  margin-left: 20px;
}

.AdminResults__index,
.AdminResults__score {
  color: #ee8208;
}

.AdminResults__remove {
  color: #999;
  cursor: pointer;
}
.AdminResults__remove:hover {
  color: #333;
}



